<template>
  <div id="process-abnormal">
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>选择井间连通模式：</span>
          <el-select v-model="train_mode" placeholder="井间连通模式">
            <el-option v-for="mode in train_modes" :key="mode.value" :label="mode.label" :value="mode.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!-- 地质背景 -->
    <el-card shadow="hover">
      <div slot="header">
        <b>
          地质背景
        </b>
      </div>
      <div style="margin-bottom: 20px;">
        <span v-if="train_mode == 1">砂潜厚度相当，整体地形较平缓，没有明显较大的倾斜度</span>
        <span v-if="train_mode == 2">砂潜山层厚度略大于砂层厚度，具有明显的倾斜度</span>
        <span v-if="train_mode == 3">砂潜山层厚度大于砂层厚度，具有明显的倾斜度，地层倾斜上升后出现水平段</span>
        <span v-if="train_mode == 4">潜山层厚度明显大于砂岩层厚度，潜山占主体，砂岩层被潜山层截断</span>
        <span v-if="train_mode == 5">砂岩层和潜山层不连通或弱连通，井与井之间存在断层</span>
      </div>
      <el-row :gutter="12">
        <el-col :span="12">
          <el-card shadow="never">
            <div slot="header">
              油藏剖面图
            </div>
            <el-image class="image" :src="src_1" :preview-src-list="[src_1]" fit="contain">
            </el-image>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card shadow="never">
            <div slot="header">
              连通模式图
            </div>
            <el-image class="image" :src="src_2" :preview-src-list="[src_2]" fit="contain">
            </el-image>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>选择训练数据源：</span>
          <el-select v-model="train_data_method" placeholder="训练数据源">
            <el-option v-for="method in train_data_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!-- 内置训练数据信息 -->
    <el-card v-if="train_data_method == 1" shadow="hover">
      <div slot="header">
        <b>
          连通模式 {{ train_mode }} 内置训练数据信息
        </b>
        <div>
          <div style="margin-top: 20px;margin-bottom: 20px;">
            共{{ mode_info.size.rows }} 行，{{ mode_info.size.columns }} 列
          </div>
          <el-tabs stretch>
            <el-tab-pane v-for="(feature, index) in mode_info.features" :label="index">
              <el-table :data="[feature]" style="width: 100%" stripe>
                <el-table-column v-for="(column, index) in Object.keys(feature)" :key="column" :prop="column"
                  :label="columns_info[index]">
                </el-table-column>
              </el-table>
              <!-- 绘制散点图 -->
              <!-- <div id="train-mode-scatter"></div> -->
            </el-tab-pane>
          </el-tabs>
        </div>

      </div>
    </el-card>
    <!-- 内置训练数据 -->
    <el-card v-if="train_data_method == 1" shadow="hover">
      <div slot="header">
        <b>
          连通模式 {{ train_mode }} 内置训练数据
        </b>
      </div>
      <div class="mode-data">
        <el-table :data="mode_data.slice((currentpage - 1) * pagesize, currentpage * pagesize)" style="width: 100%;"
          height="600" stripe>
          <!-- 行号 -->
          <el-table-column prop="" label="">
            <el-table-column prop="" label="" fixed>
              <el-table-column type="index" width="50" :index="table_index" fixed>
              </el-table-column>
            </el-table-column>
          </el-table-column>
          <!-- 表头 -->
          <el-table-column v-for="column in columns" :key="column" :prop="column" :label="labels[column].meaning">
            <el-table-column :prop="column" :label="column">
              <el-table-column :prop="column" :label="labels[column].unit">
              </el-table-column>
            </el-table-column>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div>
          <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="currentpage" :page-sizes="[25, 50, 100, 200]" :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper" :total="mode_data.length">
          </el-pagination>
        </div>
      </div>
    </el-card>
    <!-- 上传训练数据 -->
    <el-card v-if="train_data_method == 2" class="form-example" shadow="hover">
      <div slot="header">
        <b>
          上传表格示例
        </b>
      </div>
      <el-table :data="mode_data.slice(0, 10)" style="width: 100%;" stripe>
        <!-- 行号 -->
        <el-table-column type="index" width="50" fixed>
        </el-table-column>
        <!-- 表头 -->
        <el-table-column v-for="column in columns" :key="column" :prop="column" :label="column">
          <!-- <el-table-column :prop="column" :label="column">
            <el-table-column :prop="column" :label="labels[column].unit">
            </el-table-column>
          </el-table-column> -->
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="train_data_method == 2" class="form-upload" shadow="hover">
      <div slot="header">
        <el-upload class="upload-file" ref="upload" action="/" :show-file-list="false" :on-change="upload_table"
          accept=".csv,.xlsx,.xls" :auto-upload="false">
          <el-button icon="el-icon-upload" size="small" type="primary">
            上传表格文件
          </el-button>
        </el-upload>
      </div>
      <div v-if="train_data_input.length > 0">
        <el-table :data="train_data_input.slice((currentpage - 1) * pagesize, currentpage * pagesize)"
          style="width: 100%;" height="600" stripe>
          <!-- 行号 -->
          <el-table-column prop="" label="">
            <el-table-column prop="" label="" fixed>
              <el-table-column type="index" width="50" :index="table_index" fixed>
              </el-table-column>
            </el-table-column>
          </el-table-column>
          <!-- 表头 -->
          <el-table-column v-for="column in columns" :key="column" :prop="column" :label="labels[column].meaning">
            <el-table-column :prop="column" :label="column">
              <el-table-column :prop="column" :label="labels[column].unit">
              </el-table-column>
            </el-table-column>
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="160">
            <template slot-scope="scope">
              <el-button @click.native.prevent="show_add_prev_row(scope.$index + (currentpage - 1) * pagesize)"
                type="text" size="small">
                前增
              </el-button>
              <el-button @click.native.prevent="show_add_next_row(scope.$index + (currentpage - 1) * pagesize)"
                type="text" size="small">
                后增
              </el-button>
              <el-button @click.native.prevent="show_edit_row(scope.$index + (currentpage - 1) * pagesize)" type="text"
                size="small">
                编辑
              </el-button>
              <el-button @click.native.prevent="delete_row(scope.$index + (currentpage - 1) * pagesize)" type="text"
                size="small">
                移除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="currentpage" :page-sizes="[25, 50, 100, 200]" :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper" :total="mode_data.length">
        </el-pagination>
        <!-- 编辑 -->
        <el-dialog title="编辑" :visible.sync="dialog_edit">
          <el-input size="mini" v-for="column in columns" placeholder="" v-model="table_row[column]"
            style="padding-bottom: 5px;">
            <template slot="prepend">{{ column }}</template>
          </el-input>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog_edit = false">取 消</el-button>
            <el-button type="primary" @click="edit_row">确 定</el-button>
          </div>
        </el-dialog>
        <!-- 前增 -->
        <el-dialog title="前增" :visible.sync="dialog_prev">
          <el-input size="mini" v-for="column in columns" placeholder="" v-model="table_row[column]"
            style="padding-bottom: 5px;">
            <template slot="prepend">{{ column }}</template>
          </el-input>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog_prev = false">取 消</el-button>
            <el-button type="primary" @click="add_prev_row">确 定</el-button>
          </div>
        </el-dialog>
        <!-- 后增 -->
        <el-dialog title="后增" :visible.sync="dialog_next">
          <el-input size="mini" v-for="column in columns" placeholder="" v-model="table_row[column]"
            style="padding-bottom: 5px;">
            <template slot="prepend">{{ column }}</template>
          </el-input>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog_next = false">取 消</el-button>
            <el-button type="primary" @click="add_next_row">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>

<script>
import { draw_scatterplot } from "@/utils/draw";
import * as d3 from "d3-dsv";
import axios from "axios";
import * as XLSX from 'xlsx';
import { deep_copy } from "@/utils/copy";

export default {
  name: 'TrainMode',
  data() {
    return {
      // 井间连通模式
      train_modes: [
        {
          value: 1,
          label: '模式1'
        }, {
          value: 2,
          label: '模式2'
        }, {
          value: 3,
          label: '模式3'
        }, {
          value: 4,
          label: '模式4'
        }, {
          value: 5,
          label: '模式5'
        }
      ],
      // 训练数据源
      train_data_methods: [
        {
          value: 1,
          label: '内置数据'
        }, {
          value: 2,
          label: '上传数据'
        }
      ],
      // 内置数据信息表头
      columns_info: ["最大值", "最小值", "均值", "中位数", "众数", "方差", "标准差"],
      // 内置训练数据
      mode_data: [],
      // 表头
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "f1", "f2", "g"],
      // 内置训练数据表头标题
      labels: {
        "SPO": {
          meaning: "砂岩孔隙度",
          unit: ""
        },
        "BMPO": {
          meaning: "潜山基质孔隙度",
          unit: ""
        },
        "BFPO": {
          meaning: "潜山裂缝孔隙度",
          unit: ""
        },
        "SPE": {
          meaning: "砂岩渗透率",
          unit: "mD"
        },
        "BMPE": {
          meaning: "潜山基质渗透率",
          unit: "mD"
        },
        "BFPE": {
          meaning: "潜山裂缝渗透率",
          unit: "D"
        },
        "ST": {
          meaning: "砂层厚度",
          unit: ""
        },
        "BT": {
          meaning: "潜山层厚度",
          unit: "m"
        },
        "WP": {
          meaning: "井间距",
          unit: "m"
        },
        "FN": {
          meaning: "裂缝数",
          unit: ""
        },
        "SFR": {
          meaning: "砂层采收速度",
          unit: "m/s"
        },
        "BFR": {
          meaning: "潜山层采收速度",
          unit: "m/s"
        },
        "SD": {
          meaning: "地层倾角",
          unit: ""
        },
        "FP": {
          meaning: "断层封闭程度",
          unit: ""
        },
        "f1": {
          meaning: "潜山流入砂层流量占比",
          unit: ""
        },
        "f2": {
          meaning: "砂层流入潜山流量占比",
          unit: ""
        },
        "g": {
          meaning: "流量占比差",
          unit: ""
        }
      },
      // 分页器
      currentpage: 1, // 当前页码
      pagesize: 50, // 每页的数据条数
      // 表格编辑
      dialog_prev: false,
      dialog_edit: false,
      dialog_next: false,
      table_row: {},
      table_row_index: 1
    };
  },
  computed: {
    train_mode: { // 井间连通模式
      get() {
        return this.$store.state.train_mode;
      },
      set(newVal) {
        this.$store.commit('set_train_mode', newVal);
      }
    },
    train_data_method: { // 训练数据源选择
      get() {
        return this.$store.state.train_data_method;
      },
      set(newVal) {
        this.$store.commit('set_train_data_method', newVal);
      }
    },
    train_data_input: { // 训练数据源
      get() {
        return this.$store.state.train_data_input;
      },
      set(newVal) {
        this.$store.commit('set_train_data_input', newVal);
      }
    },
    src_1() {
      return require(`@/assets/imgs/mode_${this.train_mode}_1.png`);
    },
    src_2() {
      return require(`@/assets/imgs/mode_${this.train_mode}_2.png`);
    },
    mode_info() {
      return require(`@/assets/data/${this.train_mode}_info.json`);
    },
    // 导入的预测数据
    process_input_data: { // 导入的数据
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_result: { // 处理后的数据
      get() {
        return this.$store.state.process_result;
      },
      set(newVal) {
        this.$store.commit('set_process_result', newVal);
      }
    },
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
  },
  watch: {
    train_mode: function (val) {
      // 切换表头
      this.switch_columns();
      // 切换内置训练数据
      this.set_mode_data();
      // 清空导入的训练数据
      this.train_data_input = [];
      // 默认为内置数据
      this.train_data_method = 1;
      // 清空导入的预测数据
      this.process_input_data = [];
      // 清空处理后的数据
      this.process_result = [];
      this.train_process_result = [];
    }
  },
  methods: {
    // 根据模式设置内置训练数据
    set_mode_data() {
      axios.get(`/${this.train_mode}.csv`).then(res => {
        this.mode_data = d3.csvParse(res.data);
      });
    },
    // 根据模式设置表头
    switch_columns() {
      switch (this.train_mode) {
        case 1:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 2:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 3:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 4:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 5:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "FP", "SFR", "BFR", "f1", "f2", "g"];
          break;
        default:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "f1", "f2", "g"];
      }
    },
    //每页条数改变时触发 选择一页显示多少行
    handleSizeChange(val) {
      this.currentpage = 1;
      this.pagesize = val;
    },
    //当前页改变时触发 跳转其他页
    handleCurrentChange(val) {
      this.currentpage = val;
    },
    // 解决分页时行号没有累加
    table_index(index) {
      // index索引从零开始，index +1即为当前数据序号
      this.currentpage <= 0 ? this.currentpage = 1 : this.currentpage;
      // 如果当前不是第一页数据
      if (this.currentpage != 1) {
        // index + 1 + (( 当前页 - 1) * 每页展示条数)
        // 比如是第二页数据 1 + ((2 - 1)*5) = 6,第二页数据也就是从序号6开始
        return index + 1 + ((this.currentpage - 1) * this.pagesize);
      }
      // 否则直接返回索引+1作为序号
      return index + 1;
    },
    // 上传表格文件
    upload_table(file) {
      new Promise(function (resolve, reject) {
        const reader = new FileReader();
        reader.onload = function (e) {
          const data = e.target.result;
          this.wb = XLSX.read(data, {
            type: 'binary'
          });
          const result = [];
          this.wb.SheetNames.forEach((sheetName) => {
            result.push({
              sheetName: sheetName,
              columns: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName], { header: 1 })[0],
              sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName]),
            });
          });
          resolve(result);
        };
        reader.readAsBinaryString(file.raw);
      }).then(tabJson => {
        if (tabJson && tabJson.length > 0) {
          // 保存到store
          // this.process_upload_sheetname = tabJson[0].sheetName; // sheetname
          // this.process_upload_columns = tabJson[0].columns; // 表头
          this.train_data_input = tabJson[0].sheet; //表格数据
        }
      });
    },
    // 表格编辑
    init_table_row() {
      this.table_row = this.columns.reduce((acc, curr) => {
        acc[curr] = "";
        return acc;
      }, {});
    },
    delete_row(index) { // 删除整行
      this.train_data_input.splice(index, 1);
    },
    // 编辑
    show_edit_row(index) {
      this.table_row_index = index;
      this.table_row = deep_copy(this.train_data_input[index]);
      this.dialog_edit = true;
    },
    edit_row() {
      this.train_data_input[this.table_row_index] = deep_copy(this.table_row);
      this.dialog_edit = false;
    },
    // 前增
    show_add_prev_row(index) {
      this.table_row_index = index;
      this.init_table_row();
      this.dialog_prev = true;
    },
    add_prev_row() {
      this.train_data_input.splice(this.table_row_index, 0, deep_copy(this.table_row));
      this.dialog_prev = false;
    },
    // 后增
    show_add_next_row(index) {
      this.table_row_index = index;
      this.init_table_row();
      this.dialog_next = true;
    },
    add_next_row() {
      this.train_data_input.splice(this.table_row_index + 1, 0, deep_copy(this.table_row));
      this.dialog_next = false;
    },
  },
  mounted() {
    // 切换表头
    this.switch_columns();
    // 初始化内置训练数据
    this.set_mode_data();
  }
};
</script>

<style lang="scss" scoped>
#process-abnormal {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  // 保存的模型
  .el-collapse-item__header {
    font-weight: 18px !important;
    font-weight: bold !important;
  }

  // 训练数据详情
  // .mode-data {
  //   width: 100%;
  // }

  // 去除表格padding
  ::v-deep(.el-table .el-table__cell) {
    padding: 0;
  }
}
</style>