import Plotly from 'plotly.js-dist-min';

// 绘制折线图
export function draw_lineplot(id, result, tag) {
  // 直线图标题
  let title = "";
  if (tag == "f1") {
    title = "测试集中潜山流入砂岩流量占比的真实值与预测值对比";
  }
  if (tag == "f2") {
    title = "测试集中砂岩流入潜山流量占比的真实值与预测值对比";
  }
  if (tag == "g") {
    title = "测试集中流量占比差的真实值与预测值对比";
  }
  // 绘制折线图所需的数据
  const y_test = {
    x: Object.keys(result[tag].y_test),
    y: result[tag].y_test,
    type: 'lines+markers',
    name: "真实值"
  };
  const y_pred = {
    x: Object.keys(result[tag].y_pred),
    y: result[tag].y_pred,
    type: 'lines+markers',
    name: "预测值"
  };
  const data = [y_test, y_pred];
  const layout = {
    title
    // xaxis: {
    //   title: ''
    // }
  };
  const config = { responsive: true };
  Plotly.newPlot(id, data, layout, config);
}

// 绘制散点图
export function draw_scatterplot(id, data, title = "") {
  const y = {
    x: Object.keys(data),
    y: data,
    type: 'scatter',
  };
  const layout = {
    title
  };
  const config = { responsive: true };
  Plotly.newPlot(id, [y], layout, config);
}
