<template>
  <div id="predict">
    <el-alert v-if="!process_input_data.length > 0" title="请先导入数据" type="warning" show-icon :closable="false">
    </el-alert>
    <el-alert v-else-if="!process_result.length > 0" title="请先处理导入数据" type="warning" show-icon :closable="false">
    </el-alert>
    <el-alert v-else-if="!train_save.length > 0" title="请先训练并保存模型" type="warning" show-icon :closable="false">
    </el-alert>
    <div v-else>
      <!-- 选择模型 -->
      <el-card shadow="hover" style="margin-bottom: 20px;">
        <div class="setting">
          <div class="setting-method" style="margin-right: 20px;">
            <span>选择模型：</span>
            <el-select v-model="predict_select" placeholder="选择模型">
              <el-option v-for="(model, index) in train_save" :key="index + 1" :label="'模型' + (index + 1)"
                :value="index + 1">
              </el-option>
            </el-select>
          </div>
          <el-button type="primary" @click="predict_connectivity">预测连通性</el-button>
        </div>
      </el-card>
      <!-- 预测结果 -->
      <el-card v-if="predict_result.length > 0" class="info" shadow="hover">
        <div slot="header">
          <b>
            预测结果
          </b>
        </div>
        <el-table :data="predict_result.slice((currentpage - 1) * pagesize, currentpage * pagesize)" style="width: 100%;"
          :height="600" stripe>
          <!-- 行号 -->
          <el-table-column prop="" label="">
            <el-table-column prop="" label="" fixed>
              <el-table-column type="index" width="50" :index="table_index" fixed>
              </el-table-column>
            </el-table-column>
          </el-table-column>
          <!-- 表头 -->
          <el-table-column v-for="column in columns" :key="column" :prop="column" :label="labels[column].meaning">
            <el-table-column :prop="column" :label="column">
              <el-table-column :prop="column" :label="labels[column].unit">
              </el-table-column>
            </el-table-column>
          </el-table-column>

          <!-- <el-table-column type="index" width="50" fixed>
          </el-table-column>
          <el-table-column label="导入数据">
            <el-table-column v-for="column in columns_input" :key="column" :prop="column" :label="column">
            </el-table-column>
          </el-table-column>
          <el-table-column label="预测结果">
            <el-table-column v-for="column in columns_predict" :key="column" :prop="column" :label="column">
            </el-table-column>
          </el-table-column> -->
        </el-table>
        <!-- 分页 -->
        <div>
          <el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="currentpage" :page-sizes="[5, 10, 50, 100]" :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper" :total="predict_result.length">
          </el-pagination>
        </div>

      </el-card>
    </div>
  </div>
</template>

<script>
import { predict } from "@/api";


export default {
  name: 'predict',
  data() {
    return {
      // 表头
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "f1", "f2", "g"],
      // 内置训练数据表头标题
      labels: {
        "SPO": {
          meaning: "砂岩孔隙度",
          unit: ""
        },
        "BMPO": {
          meaning: "潜山基质孔隙度",
          unit: ""
        },
        "BFPO": {
          meaning: "潜山裂缝孔隙度",
          unit: ""
        },
        "SPE": {
          meaning: "砂岩渗透率",
          unit: "mD"
        },
        "BMPE": {
          meaning: "潜山基质渗透率",
          unit: "mD"
        },
        "BFPE": {
          meaning: "潜山裂缝渗透率",
          unit: "D"
        },
        "ST": {
          meaning: "砂层厚度",
          unit: ""
        },
        "BT": {
          meaning: "潜山层厚度",
          unit: "m"
        },
        "WP": {
          meaning: "井间距",
          unit: "m"
        },
        "FN": {
          meaning: "裂缝数",
          unit: ""
        },
        "SFR": {
          meaning: "砂层采收速度",
          unit: "m/s"
        },
        "BFR": {
          meaning: "潜山层采收速度",
          unit: "m/s"
        },
        "SD": {
          meaning: "地层倾角",
          unit: ""
        },
        "FP": {
          meaning: "断层封闭程度",
          unit: ""
        },
        "f1": {
          meaning: "潜山流入砂层流量占比",
          unit: ""
        },
        "f2": {
          meaning: "砂层流入潜山流量占比",
          unit: ""
        },
        "g": {
          meaning: "流量占比差",
          unit: ""
        }
      },
      // 分页器
      currentpage: 1, // 当前页码
      pagesize: 10 // 每页的数据条数
      // columns_input: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      // columns_predict: ["f1", "f2", "g"],
    };
  },
  computed: {
    train_mode: { // 井间连通模式
      get() {
        return this.$store.state.train_mode;
      },
      set(newVal) {
        this.$store.commit('set_train_mode', newVal);
      }
    },
    process_input_data: { // 导入的数据
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_result: { // 处理后的导入数据
      get() {
        return this.$store.state.process_result;
      },
      set(newVal) {
        this.$store.commit('set_process_result', newVal);
      }
    },
    train_process_result: { // 内置数据处理结果
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_save: { // 保存的模型
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
    predict_select: { // 选中的模型
      get() {
        return this.$store.state.predict_select;
      },
      set(newVal) {
        this.$store.commit('set_predict_select', newVal);
      }
    },
    predict_setting: { // 预测请求参数
      get() {
        return this.$store.state.predict_setting;
      },
      set(newVal) {
        this.$store.commit('set_predict_setting', newVal);
      }
    },
    predict_result: { // 预测结果
      get() {
        return this.$store.state.predict_result;
      },
      set(newVal) {
        this.$store.commit('set_predict_result', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新predict_setting
    predict_setting: {
      handler(newVal, oldVal) {
        this.predict_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    predict_connectivity() {
      // 发送请求
      this.predict_setting = {
        data_train: this.train_process_result,
        data_predict: this.process_input_data,
        test_size: this.train_save[this.predict_select - 1].train_algorithm_setting.test_size,
        algorithm: this.train_save[this.predict_select - 1].train_algorithm_setting.algorithm,
        paras: this.train_save[this.predict_select - 1].train_algorithm_setting.paras
      };
      predict(this.predict_setting).then(result => {
        console.log(JSON.parse(result));
        this.predict_result = JSON.parse(result);
      });
    },
    // 根据模式设置表头
    switch_columns() {
      switch (this.train_mode) {
        case 1:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 2:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 3:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 4:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SD", "SFR", "BFR", "f1", "f2", "g"];
          break;
        case 5:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "FP", "SFR", "BFR", "f1", "f2", "g"];
          break;
        default:
          this.columns = ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR", "f1", "f2", "g"];
      }
    },
    //每页条数改变时触发 选择一页显示多少行
    handleSizeChange(val) {
      this.currentpage = 1;
      this.pagesize = val;
    },
    //当前页改变时触发 跳转其他页
    handleCurrentChange(val) {
      this.currentpage = val;
    },
    // 解决分页时行号没有累加
    table_index(index) {
      // index索引从零开始，index +1即为当前数据序号
      this.currentpage <= 0 ? this.currentpage = 1 : this.currentpage;
      // 如果当前不是第一页数据
      if (this.currentpage != 1) {
        // index + 1 + (( 当前页 - 1) * 每页展示条数)
        // 比如是第二页数据 1 + ((2 - 1)*5) = 6,第二页数据也就是从序号6开始
        return index + 1 + ((this.currentpage - 1) * this.pagesize);
      }
      // 否则直接返回索引+1作为序号
      return index + 1;
    }
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#predict {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }

  // 去除表格内边距
  ::v-deep(.el-table__cell) {
    padding: 0;
  }
}
</style>