<template>
  <div id="process-abnormal">
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span>是否处理异常值：</span>
          <el-select v-model="process_setting.abnormal[0].state" placeholder="是否处理异常值">
            <el-option v-for="state in process_abnormal_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>异常值检测方法：</span>
          <el-select v-model="process_setting.abnormal[0].detect" placeholder="异常值检测方法"
            :disabled="!process_setting.abnormal[0].state">
            <el-option v-for="detect in process_abnormal_detects" :key="detect.value" :label="detect.label"
              :value="detect.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>异常值处理方法：</span>
          <el-select v-model="process_setting.abnormal[0].method" placeholder="异常值处理方法"
            :disabled="!process_setting.abnormal[0].state">
            <el-option v-for="method in process_abnormal_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          方法介绍
        </b>
      </div>
      <div style="margin-bottom: 20px;">
        <div style="margin-bottom: 20px;">
          <h3>异常值检测方法</h3>
        </div>
        <div style="padding-left: 20px;">
          <p><b>IQR：</b>大于第四分位数与小于第一四分位数的数据为异常值</p>
          <p><b>Z-score：</b>分布在距离平均数三个标准差之外的数据为异常值</p>
        </div>
      </div>
      <div>
        <div style="margin-bottom: 10px;">
          <h3>异常值处理方法</h3>
        </div>
        <div style="padding-left: 20px;">
          <p><b>均值替换：</b>将异常值替换为异常值所在的那列的均值</p>
          <p><b>中位数替换：</b>将异常值替换为异常值所在的那列的中位数</p>
          <p><b>众数替换：</b>将异常值替换为异常值所在的那列的众数</p>
          <p><b>边界值替换：</b></p>
          <p>检测方法为IQR时，将大于第四分位数的数据替换为第四分位数，小于第一分位数的数据替换为第一分位数</p>
          <p>检测方法为Z-score时，将大于平均数+三个标准差的数据替换为平均数+三个标准差处的数据，小于平均数-三个标准差的数据替换为平均数-三个标准差处的数据</p>
        </div>


      </div>
    </el-card>
    <!-- <el-card v-if="process_input_data.length > 0" class="condition" shadow="hover">
      <div slot="header">
        <b>
          异常值情况
        </b>
      </div>
      <div id="process-boxplot">
        11
      </div>
    </el-card> -->
  </div>
</template>

<script>
import Plotly from 'plotly.js-dist-min';


export default {
  name: 'ProcessAbnormal',
  data() {
    return {
      // 是否处理异常值
      process_abnormal_states: [
        {
          value: 0,
          label: '否'
        }, {
          value: 1,
          label: '是'
        }
      ],
      // 异常值检测方法
      process_abnormal_detects: [
        {
          value: 1,
          label: 'IQR'
        }, {
          value: 2,
          label: 'Z-score'
        }
      ],
      // 异常值处理方法
      process_abnormal_methods: [
        // {
        //   value: 1,
        //   label: '删除'
        // }, 
        {
          value: 2,
          label: '均值替换'
        }, {
          value: 3,
          label: '中位数替换'
        }, {
          value: 4,
          label: '众数替换'
        }, {
          value: 5,
          label: '边界值替换'
        }
      ],
      // 绘制箱线图
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
    };
  },
  computed: {
    process_input_data: { // 导入的数据，用于绘制箱线图
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 处理方法参数选择
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
    process_result: { // 处理后的数据
      get() {
        return this.$store.state.process_result;
      },
      set(newVal) {
        this.$store.commit('set_process_result', newVal);
      }
    },
    train_process_result: { // 处理后的内置数据
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
  },
  watch: {
    // 实现深度改变时更新process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
        // 清空处理后的数据
        this.process_result = [];
        this.train_process_result = [];
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 绘制箱线图
    draw_boxplot() {
      // 整理每列数据
      let data_coloum = {};
      this.columns.map(column => data_coloum[column] = []);
      this.process_input_data.map(row_data => {
        for (let coloum in data_coloum) {
          if (typeof row_data[coloum] != 'undefined') {
            data_coloum[coloum].push(row_data[coloum]);
          }
        }
      });
      // 构造箱线图数据
      let data = [];
      this.columns.map(column => {
        data.push({
          y: data_coloum[column],
          type: 'box',
          name: column,
          marker: {
            color: 'rgb(8,81,156)',
            outliercolor: 'rgba(219, 64, 82, 0.6)',
            line: {
              outliercolor: 'rgba(219, 64, 82, 1.0)',
              outlierwidth: 2
            }
          },
          boxpoints: 'suspectedoutliers'
        });
      });
      const layout = {
        // title: '箱线图'
        autosize: true, // 自动跳转大小
        showlegend: false, // 隐藏图例
      };
      Plotly.newPlot('process-boxplot', data, layout);
      // 保存到store
      this.process_boxplot = data;
    }
  },
  mounted() {
    // 绘制箱线图。测试发现由于数据差距非常大，基本上都压成一条直线没有高度了
    // if (this.process_input_data.length > 0) {
    //   this.draw_boxplot();
    // }
  }
};
</script>

<style lang="scss" scoped>
#process-abnormal {

  // 每个card设置下间距
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>